.SeedList{
    border: 2px solid vaR(--main-purple);
    border-radius: 8px;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.515);
    max-width: 20rem;
    margin-inline: auto;
    transition: border-color 0.3s;
    overflow: hidden;
}

.SeedList:hover{
    border-color: white;
}

.SeedList-detail{
    padding: 1rem;
}

.SeedList-thumbnail{
    /* height: 10rem; */
}

.SeedList-symbol{
    margin-bottom: 0rem;
    font-weight: 600;
    font-size: 1.4rem;
}

.SeedList-name{
    font-size: 0.8rem;
    margin-top: 0.2rem;
    font-weight: 500;
}

.SeedList-thumbnail-img{
    width: 100%;
}

.Seed-introduction-logo{
    width: 6rem;
    height: 6rem;
}

.Seed-introduction-name{
    margin-bottom: 0.1rem;
    margin-right: 2rem;
}

.Seed-introduction-badge{
    width: fit-content;
    padding: 0.5rem 1rem
}

.Seed-introduction-desc{
    font-size: 0.8rem;
}

.Seed-sale-item{
    padding: 0.8rem;
    margin-bottom: 0.6rem;
}

.Seed-sale-item-amount{
    font-weight: bold;
    white-space: nowrap;
}

.Seed-introduction-price{
    background-color: var(--main-purple);
    padding: 0.5rem 1rem;
    border-radius: 1rem;
    font-size: 1rem;
}

.Seed-progress-label{
    justify-content: space-between;
}

.progress-percent-container{
    font-size: 0.7rem;
}

.progress-softcap{
    font-size: 0.7rem;
}

.Seed-max-btn{
    padding: 0.2rem 1rem;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.Seed-buy-amount{
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.buy-result{
    font-size: 0.8rem;
    margin-top: 0.2rem;
}

.sale-time{
    font-size: 0.8rem;
    font-weight: bold;
}