:root {
  --animate-delay: 0.2s;
  --main-purple: #904BFF;
  --main-green: #6fd03c;
  --error-red: #ff4141;
  --black-40: #00000040;
}

body {
  margin: 0;
}

div {
  color: white;
}

.d-block{
  display: block;
}

.w-fc{
  width: -webkit-fit-content !important;
  width: -moz-fit-content !important;
  width: fit-content !important;
}

.pages{
  min-height: 98vh;
  padding-top: 2rem;
}

.purple-input{
  background-color: transparent;
  color: white;
  border: 1px solid #904BFF;
  border: 1px solid var(--main-purple);
}

.purple-input:focus{
  background-color: transparent;
  color: white;
  border: 1px solid white;
  box-shadow: 0 0 0.5rem 0.3rem #c300ffa8;
}

.header-wrapper{
  position: fixed;
  height: 5rem;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  z-index: 5;
  justify-content: space-between;
  padding: 0rem 1rem;
  /* background-color: #00000066; */
  pointer-events: none;
}

.pe-all{
  pointer-events: all;
}

@media (min-width: 0px) {
  html {
    font-size: 12px;
  }

  .home-btn-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 42rem;
  }

  .header-logo{
    position: absolute;
    left: 1rem;
    top: 1rem;
    width: 10rem;
  }
}

@media (min-width: 300px) {
  html {
    font-size: 14px;
  }
}

@media (min-width: 450px) {
  html {
    font-size: 16px;
  }
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
  html {
    font-size: 20px;
  }
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  html {
    font-size: 20px;
  }
  
  .home-btn-container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    max-width: 42rem;
    padding-left: 1rem;
  }

  .header-logo{
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: 1rem;
    width: 14rem;
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {}

