/* DECODED TEXT */

.decode-text {
  /* width: 100%; */
  font-size: 1.5rem;
  text-align: center;
}


.space {
  display: inline-block;
  width: 10px;
}

.text-animation {
  display: inline-block;
  position: relative;
  color: transparent;
  text-transform: uppercase;
}

.text-animation:before {
  content: "";
  color: #FFFFFF;
  position: absolute;
  top: 50%;
  left: 50%;
  background: #FFFFFF;
  width: 0;
  height: 1.2em;
  -webkit-transform: translate(-50%, -55%);
  transform: translate(-50%, -55%);
}

.text-animation.state-1:before {
  width: 1px;
}

.text-animation.state-2:before {
  width: 0.9em;
}

.text-animation.state-3 {
  color: #FFFFFF;
}

.text-animation.state-3:before {
  width: 0;
}

#refresh {
  position: absolute;
  top: 20px;
  left: 20px;
  cursor: pointer;
}

